<!-- eslint-disable no-unused-vars -->
<template>
	<div>
		<div :id="uniqueId" ref="plot_" style="margin-top: 10px; width: 100%; height:100%;"></div>
	</div>
</template>

<script>
	import '@material/mwc-menu';
	import * as echarts from 'echarts';
	import 'echarts-gl';
	import '@material/mwc-circular-progress/mwc-circular-progress.js'

	// let url = {};
	let charts = null;

	export default {
		props: ['data'],
		data() {
			return {
				loading: false,
				error: false,
				plotOption: null,
        uniqueId: Date.now().toString(36) + Math.random().toString(36).substring(2)
			}
		},
		mounted() {
			this.plotOption = JSON.parse(this.data)['options']
			this.plot()
		},
		methods: {
			plot() {
				if(this.plotOption) {
					// if(charts) {
					// 	charts.dispose()
					// }
					charts = echarts.init(this.$refs.plot_, {}, {width: this.plotOption['figsize']['width'], height: this.plotOption['figsize']['height']})
					charts.setOption(this.plotOption)

					if (this.plotOption['event']['type_'] !== null){
						if (this.plotOption['event']['purpose'] === 'filter'){
							this.plotOption['initial_data_target'] = []
							for (var tar_ind = 0; tar_ind < this.plotOption['event']['data_target'].length; tar_ind++) {
									var new_tar = this.plotOption['event']['data_target'][tar_ind]
									this.plotOption['initial_data_target'].push(this.plotOption['series'][new_tar]['data']);
								}
							charts.on(this.plotOption['event']['type_'], (params) => {
								var brushed = [];
								var data = this.plotOption['initial_data_target']
								var data_source_ind = this.plotOption['event']['data_source']
								var brushComponent = params.batch[0];
								for (var sIdx = 0; sIdx < brushComponent.selected.length; sIdx++) {
									var rawIndices = brushComponent.selected[sIdx].dataIndex;
									brushed.push(rawIndices);
								}
								for (var tar_ind_ = 0; tar_ind_ < this.plotOption['event']['data_target'].length; tar_ind_++) {
									var tmp = []
									var new_tar = this.plotOption['event']['data_target'][tar_ind_]
									console.log(data[tar_ind_])
									for (var sIdx2 = 0; sIdx2 < brushed[data_source_ind].length; sIdx2++) {
										tmp.push(data[tar_ind_][brushed[data_source_ind][sIdx2]])
									}
									// tmp.sort(function(a,b) {
									// 	return a[0]-b[0]
									// });
									console.log('changed')
									console.log(tmp)
									this.plotOption['series'][new_tar]['data'] = tmp
								}
								
								charts.setOption(this.plotOption);
								})
							}
						}
				}
			}
		}
	}
</script>

<style scoped>

.container {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
}

.select-widget {
	width: 30%;
	min-width: 220px;
	max-width: 300px;
}

@media (max-width: 1250px) {
	.select-widget {
		width: 46%; 
		max-width: 300px;
	}
}

.progressbar {
	display: flex; 
	align-items: center;
	justify-content: center;
	padding: 100px 0px 100px 0px;
}

</style>